<template>
	<div>
		<div class="text-right mb-2">
			<button class="btn btn-primary" @click="showAdd"><i class="fas fa-plus"></i> Tambah Kewajiban Pelaporan</button>
		</div>
		<table class="table">
			<thead>
				<tr>
					<th>No</th>
					<th>Instansi</th>
					<th>Jenis Laporan</th>
					<th>Pelaporan Terakhir</th>
					<th>Dokumen Terakhir</th>
					<th>Laporan Selanjutnya</th>
					<th>Aksi</th>
				</tr>
			</thead>
			<tbody>
				<tr v-if="datas.length == 0">
					<td colspan="7" class="text-center">Belum Ada Data</td>
				</tr>
				<tr v-else v-for="(v,i) in datas" :key="i">
					<td>{{ i+1 }}</td>
					<td>{{ v.instance }}</td>
					<td>{{ v.type }}</td>
					<td>{{ v.report.last }}</td>
					<td>
						<a :href="v.lastDocument.link" target="_blank">
							{{ v.lastDocument.file }}
						</a>
					</td>
					<td>{{ v.report.next }}</td>
					<td>
						<button class="btn btn-success btn-sm mr-1" @click="showDetail(v.id, v.template.link)">
							<i class="fas fa-file"></i>
						</button>
						<button class="btn btn-warning btn-sm" @click="showEdit(v)">
							<i class="fas fa-edit"></i>
						</button>
						<button class="btn btn-danger btn-sm ml-1" @click="showDelete(v.id)">
							<i class="fas fa-trash"></i>
						</button>
					</td>
				</tr>
			</tbody>
		</table>
		
		<modal-form		
		:instances="instances"
		:pics="pics"
		:id="id"
		:document="document"
		:reportingNames="reportingNames"
		:getReport="getReport"
		ref="modalForm"
		></modal-form>
		<modal-detail
		ref="modalDetail"
		></modal-detail>
		
	</div>
</template>

<script type="text/javascript">
	
	import ModalForm from './form/Report.vue'
	import ModalDetail from './detail/Report.vue'
	import documents from '@/mixins/document'
	import alert from '@/mixins/alert'
	import utils from '@/mixins/util'
	import { documentById } from '@/api/document'
	import { reportingByDocument, reportingDelete } from '@/api/reporting'
	import { reportNameAll } from '@/api/reportName'
	
	export default{
		name: 'Report',
		props: [
		'id'
		],
		mixins: [
		documents,
		alert,
		utils,
		],
		components: {
			ModalForm,
			ModalDetail,
		},
		data(){
			return {
				reportingNames: [],
				datas: [],
				instances: [],
				pics: [],
				document: null,
				docPermitNames: [],
			}
		},
		async mounted() {
			this.showLoading();
			this.instances = await this.getInstance();
			this.pics = await this.getPersonInCharge();
			this.docPermitNames = await this.getDocPermitName();
			await this.getDocument();
			await this.getReport();
			await this.getReportingName();
			this.hideLoading();
		},
		methods: {
			async getDocument(){
				this.document = null;
				
				try{
					let request = await documentById(this.id);
					let parsed = request.data[0].document;
					parsed.labeled_name = this.selectedData(this.docPermitNames, parsed.permitId);
					this.document = parsed;
				}catch(e){
					console.log(e);
					if(error.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			async getReport(){
				this.datas = [];
				try{
					let request = await reportingByDocument(this.id);
					let data = request.data;
					
					data.forEach((v) => {
						
						let previousReportingTimeTable = null;
						let lastReport = null;
						let nextReport = null;
						let lastDocument = null;
						
						v.reportingTimeTables?.forEach((timeTable) => {
							if(!nextReport){
								if(timeTable.isReported == 1 && timeTable.reportedFilesList.length > 0){
									lastReport = this.formatDate(timeTable?.time);
									lastDocument = timeTable.reportedFilesList?.[timeTable.reportedFilesList?.length - 1]?.fileAttachment?.path;
									previousReportingTimeTable = timeTable?.id;
								}
								if(lastReport && (timeTable.isReported != 1 || timeTable.reportedFilesList.length == 0) && timeTable.previousReportingTimeTableId == previousReportingTimeTable){
									nextReport = this.formatDate(timeTable?.time);
								}
							}
						})
						
						this.datas.push({
							id: v.id,
							instance: v.instance?.name,
							type: v.reportType?.name,
							report: {
								last: lastReport,
								next: nextReport,
							},
							lastDocument: {
								file: lastDocument,
								link: process.env.VUE_APP_API_URL + "/api/reporting/get_file/name?fileName=" + escape(lastDocument),
							},
							template: {
								file: v.attachment?.path,
								link: process.env.VUE_APP_API_URL + "/api/reporting/get_file/name?fileName=" + escape(v.attachment?.path),
							},
						});
					})
				}catch(e){
					console.log(e);
				}
			},
			async getReportingName(){
				this.reportingNames = [];
				try{
					let request = await reportNameAll();
					let data = request.data;
					
					data.forEach((v,i) => {
						this.reportingNames.push({
							value: v.id,
							label: v.name,
						})
					})
				}catch(e){
					console.log(e);
				}
			},
			showAdd(){
				this.$refs.modalForm.showAdd();
				$("#modalAdd").modal("show");
			},
			showDetail(id, format){
				this.$refs.modalDetail.getDetail(id, format);
				$("#modalDetail").modal("show");
			},
			showEdit(data){
				this.$refs.modalForm.showEdit(data);
			},
			showDelete(id){
				this.$swal({
					title: 'Hapus Data',
					text: "Yakin ingin menghapus data ?",
					type: 'error',
					showCancelButton: true,
					confirmButtonColor: '#3085d6',
					cancelButtonColor: '#d33',
					confirmButtonText: 'Yes'
				}).then((result) => {
					if (result.value) {
						this.showLoading();
						
						let request = reportingDelete(id);
						request
						.then((response) => {
							this.hideLoading();
							
							this.$toast.open({
								message: 'Laporan Berhasil Dihapus !',
								type: 'info',
								position: 'top-right',
							});
							
							this.getReport();
						})
						.catch((error) => {
							this.hideLoading();
							this.showNotif("Gagal Menghapus Data");
							console.log(error);
							if(error.response?.status == 401){
								this.invalidSession();
							}
						})
					}
				});
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>